.comment-navigation,
.posts-navigation,
.post-navigation,
.paging-navigation {
  padding: 2em 0;
  text-align: left;
  overflow: hidden;

  a {
	display: block;
	width: 100%;
	text-decoration: none;
	padding: 1em;
	border: 1px solid rgba(0, 0, 0, 60%);

	&:hover,
	&:focus {
	}
  }
}

.nav-links {
  margin: 0 auto;
  padding: 0 0.4em;
}

.nav-previous {
  padding-bottom: 1em;
}

.post-navigation {
  .nav-previous {
	& a::before {
	  content: 'Previous Post: \A\A';
	  font-size: 1.25em;
	  font-weight: 700;
	  margin-right: 1em;
	  white-space: pre;
	  color: #000;
	}
  }
}

.post-navigation {
  .nav-next {
	& a::before {
	  content: 'Next Post: \A\A';
	  font-size: 1.25em;
	  font-weight: 700;
	  margin-right: 1em;
	  white-space: pre;
	  color: #000;
	}
  }
}

@media screen and (min-width: 43.75em) {
  .nav-links {
	display: flex;
	justify-content: space-between;
	align-items: stretch;
	padding: 0;
  }

  .comment-navigation,
  .posts-navigation,
  .post-navigation {
	.nav-previous,
	.nav-next {
	  float: left;
	  display: flex;
	  align-items: stretch;
	  width: 48%;
	}

	.nav-next {
	  float: right;
	  text-align: right;
	}
  }

  .nav-previous {
	padding-bottom: 0;
  }
}

.post-navigation {
  .meta-nav {
	display: block;
  }

  @media screen and (min-width: 30em) {
	padding-bottom: 1em;
  }

  .post-title {
	font-size: 1.2em;
	font-weight: 700;
	border-bottom: 3px solid black;
  }

  a:hover .post-title,
  a:focus .post-title {
	border-bottom-color: black;
  }
}

.pagination {
  .nav-links {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
  }

  a,
  span {
	padding: 0.5em 1em;
  }

  a {
	text-decoration: none;

	&:focus,
	&:hover {
	}
  }

  .current {
	font-weight: 600;
  }

  .prev .icon {
	margin-right: 1em;
  }

  .next .icon {
	margin-left: 1em;
  }
}
