.widget-area {
  grid-column: 2/-2;
  margin-top: 1.5em;
}

/* Footer widgets for archive pages */
.archive-view {
  @media screen and (min-width: 43.75em) {
	&.has-sidebar {
	  clear: both;
	  display: flow-root;

	  .footer-widgets {
		width: 100%;
	  }
	}
  }
}

.widget {
  margin: 0 0 2em;
  padding: 0 0 2em;

  ul,
  ol {
	padding: 0.7em 0 0;
	margin-bottom: 0;
	list-style-type: none;

	li {
	  display: block;
	  line-height: 2em;

	  ul,
	  ol {
		margin-left: 0.5em;

		@media screen and (min-width: 30em) {
		  margin-left: 1em;
		}
	  }
	}
  }

  a {
	font-weight: 600;
	text-decoration: none;

	&:hover,
	&:focus {
	  border-bottom: 3px solid $color__link;
	}
  }

  /* Make sure select elements fit in widgets. */
  select {
	width: 60%;
	height: 2.5em;
	padding: 10px;
	border: 1px solid #ccc;
  }
}

.widget {
  list-style-type: none;

  .widgettitle {
	margin-top: 0;
	margin-bottom: 0.5em;
	font-size: 1.4em;
  }
}

/* Archive and Categories widgets */
.widget_archive li {
  color: #7d7d7d;
}

.widget_categories .children {
  padding-top: 0;
}

/* Search widget. */
.widget_search .search-submit {
  display: none;
}

/* Pages and Menu widgets */
.widget_pages .children,
.widget_nav_menu .sub-menu {
  padding-top: 0;
  padding-bottom: 0.5em;
}

.widget_pages,
.widget_nav_menu {
  a {
	display: block;
	padding: 1em 0;
	text-decoration: none;
	border-bottom: 1px dashed hsl(0, 0%, 50%);

	&:hover,
	&:focus {
	  text-decoration: none;
	  border-bottom: 1px dashed hsl(0, 0%, 50%);
	}
  }

  li {
	padding: 0;
  }
}

/* Meta widget */
.widget_meta li {
  padding: 0;
}

/* Recent comments */
.widget_recent_comments {
  li {
	line-height: 1.3em;
	padding-bottom: 0.5em;
  }

  a:hover,
  a:focus {
	padding-left: 0;
  }
}

/* RSS widget */
.widget_rss li {
  margin-bottom: 1em;
}

.widget li a.rsswidget {
  padding-right: 0.5em;
  font-size: 1em;
  line-height: 1.4em;
}

.rss-date,
.widget_rss cite {
  color: #333;
  font-size: 0.8em;
}

.rssSummary {
  padding: 0.5em 0;
  font-size: 0.8em;
  line-height: 1.4em;
}

/* Search widget */
.widget_search input[type='search'] {
  box-sizing: border-box;
  width: 60%;
  padding: 0.5em 1em;
  border: 1px solid #c3c3c3;
  border-radius: 0;
}

/* Calendar widget */
.widget_calendar {
  font-weight: 400;

  caption {
	padding: 0.5em 0;
	font-size: 1em;
	font-weight: 500;
	text-align: left;
  }

  thead {
	background: transparent;
	color: black;

	th {
	  border-bottom-width: 2px;
	}
  }

  td {
	padding: 0.2em;
	font-size: 0.8em;
	text-align: center;
	background: white;
	border: 1px solid hsl(0, 0%, 80%);
  }

  th {
	border: none;
	text-align: center;
  }

  .pad {
	background: #eaeaea;
  }

  a {
	font-weight: 900;
	text-decoration: none;
  }
}

/* Text widget */
.textwidget {
  font-size: 80%;
  line-height: 1.3em;
}

/* Footer widget stylings */
.footer-widgets {
  display: flex;
  flex-flow: row;
  gap: 4em;
}

.footer-widgets-block-1,
.footer-widgets-block-2 {
  display: flex;
  flex-flow: column;
  // margin: 2em 0 0;
  padding: 1em 0;
}

.footer-widgets-block-1 {
  flex: 1 1 50%;
}

.footer-widgets-block-2 {
  flex: 1 1 50%;
}

