.archive-view {
  background-color: white;

  .post,
  .page {
	position: relative;
	// background: #eee;
	// background: rgba(238, 238, 238, 0.45);
	padding: 2em 2em 3em;
  }

  @media screen and (min-width: 43.75em) {
	@supports not (display: grid) {
	  &.has-sidebar {
		.site-content {
		  display: flex;
		  justify-content: space-between;
		  flex-wrap: wrap;
		}

		.page-header {
		  width: 100%;
		  flex: 1 0 100%;
		}

		.content-area {
		  width: 60%;
		}

		.widget-area {
		  width: 30%;
		}
	  }
	}
  }
}

.page-header {
}

.page-title {
  margin: 0 0 0.5em;
}

.archive-description {
  margin: -1em 0 2em;
}

