.index-image {
  img {
	display: block;
	margin: 0 auto;
  }

  a {
	&:focus > img,
	&:hover > img {
	  opacity: 0.8;
	}
  }
}

.continue-reading {
  position: absolute;
  right: 0;
  bottom: 0;

  a {
	display: block;
	padding: 0.5em 1em;
	font-size: 90%;
	text-decoration: none;
	background: rgba(255, 255, 255, 0.5);

	&:focus,
	&:hover {
	  color: white;
	  background: $color__link;
	}
  }
}
